import Link from 'next/link';
import { request } from 'graphql-request';

const Post = props => (
  <div>
    test
    <h1>{props.length}</h1>
  </div>
);

Post.getInitialProps = async function(context) {
  console.log(context.query);
  const routeName = context.query.id;
  const gqlq = `{
      route(routeName: "${routeName}"){
          sailings {
              vessel
          }
      }
  }`;
  const res = await request('https://server-dubymzzcjd.now.sh/graphql', gqlq);
  console.log(`Route info fetched. Sailings: ${Object.keys(res.route.sailings)}`);
  return res;
};

export default Post;



